import {Component, ElementRef, Renderer2, ViewChild} from '@angular/core';
import {IonicPage, NavController, NavParams, ViewController} from 'ionic-angular';

/**
 * Generated class for the EmailConfirmationPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-email-confirmation',
  templateUrl: 'email-confirmation.html',
})
export class EmailConfirmation {

  email = 'mail@mail.de'
  constructor(public navCtrl: NavController, public navParams: NavParams, public viewCtrl: ViewController,private renderer: Renderer2) {
    if (this.navParams.get('selectedEmail')){
      this.email = this.navParams.get('selectedEmail')
    }
  }
  ngAfterViewInit() {
    const modalWrapper = document.querySelector('.modal-wrapper');
    if (modalWrapper) {
      this.renderer.setStyle(modalWrapper, 'height', '190px');
    }
  }
  handleCloseButton(){
    this.viewCtrl.dismiss();
    const modalWrapper = document.querySelector('.modal-wrapper');
    if (modalWrapper) {
      this.renderer.setStyle(modalWrapper, 'height', '100%');
    }
  }
  ionViewDidLoad() {
    console.log('ionViewDidLoad EmailConfirmationPage');
  }

}



